// ナビゲーション矢印
.new-post__buttons {
  width: rem(156);
  min-height: rem(46);
  position: relative; // ナビゲーション矢印の配置
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(64);
  margin: rem(40) auto 0;
  @include mq(md) {
    width: rem(132);
    gap: rem(40);
  }
}

.new-post__prev,
.new-post__next {
  width: rem(46);
  height: rem(46);
}

.new-post__prev img,
.new-post__next img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}